.Banner {
    object-fit: contain;
    height: 448px;
    color: white;

}

.Banner-Contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;

}

.Banner-Title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;


}

.Banner-description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
   
    font-size: 0.9rem;
    max-width: 360px;
    height: 50px;
}

.Banner-Button {
    cursor: pointer;
    color: #ffffff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);


}

.Banner-Button:hover {
    color: #000000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.Banner-fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111111);
}